<template>
    <div class="login-box">
        <el-row class="tab-bar">
            <el-col :span="24" >
                <span class="active">至尊宝后台登录</span>
            </el-col>
        </el-row>
        <el-form :model="loginForm" :rules="rules" ref="ruleForm">
            <el-form-item prop="name" label="用户账号">
                <el-input placeholder="请输入账号" v-model="loginForm.name"></el-input>
            </el-form-item>
            <el-form-item prop="password" label="密码">
                <el-input type="password" placeholder="请输入密码" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-button type="primary" @click="login('ruleForm')" style="width: 100%" class="main-bg main-border" :loading="btnLoading">登录</el-button>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            loginForm: {
                name: '',
                password: '',
            },
            rules: {
                name: [{ required: true, message: '请输入账号', trigger: 'blur' }],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, message: '请输入 6 位以上字符', trigger: 'blur' }],
            },
            btnLoading: false
        }
    },
    props: ['API'],
    methods: {
        login(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.btnLoading = true
                    var json = {
                        "head": {},
                        "body": {
                            "account": this.loginForm.name,
                            "password": this.loginForm.password
                        }
                    }
                    this.$common._post('login', json, this, 
                        data => {
                            this.btnLoading = false
                            if (!data.managerLogin) {
                                this.$message('权限不足')
                                return
                            }
                            this.$message({
                                message: data.description,
                                type: 'success'
                            })
                            var loginInfo = {
                                userName: this.loginForm.name,
                                userPassword: this.loginForm.password,
                            }
                            sessionStorage.setItem('zhizunbao_login', JSON.stringify(loginInfo))
                            sessionStorage.setItem('power', JSON.stringify(data.managerLogin.managerPowerChild))
                            this.$router.push('/news')
                        },
                        data => {
                            this.btnLoading = false
                            return false
                        },
                        data => {
                            this.btnLoading = false
                            return false
                        }
                    )
                }
                return false;
            })
        },
    },
    mounted(){
        this.loginForm.name = ''
        this.loginForm.password = ''
    }
}
</script>

<style scoped lang="scss">
    body, html {
        background: #7f7f7f;
    }
    .login-box {
        margin: 0 auto;
        max-width: 360px;
        margin-top: 100px;
        font-size: 22px;
        color: #333;
        .tab-bar {
            text-align: center;
            line-height: 60px;
            color: rgb(242,141,1);
        }
    }
</style>



